<template>
  <div class="text">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content2">
      <div class="content-title">
        <h2>{{ title }}</h2>
      </div>
      <div class="leftmenus">
        <div class="menus">
          <div
            class="menus-item"
            :class="[ index === menusIndex ? 'active' :　'' ]"
            v-for="(item, index) in menus"
            :key="index"
            @click="change(index)"
          >
            <img :src="mmm" />
            <span>{{ item.value }}</span>
          </div>
        </div>
      </div>
      <div class="rightcontent">
        <router-view/>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import mmm from "@/assets/icon-m.png";
import { getCurrencyType } from '@/api/asset'

export default {
  components: {
    AoNav
  },
  data() {
    return {
      title: this.$t('asset.index_title'),
      mmm, 
      menusIndex: 0,
      menus: []
    };
  },
  mounted() {
    getCurrencyType().then(res => {
      this.menus = res
    })
  },
  methods: {
    change(index) {
      this.menusIndex = index
      this.$router.push({ path: '/table' + (index + 1) })
    }
  }
};
</script>

<style lang="scss">
.text {
  .content2 {
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
    // overflow: hidden;
    min-height: 880px;
  }
  .content-title {
    height: 60px;
    line-height: 60px;
    padding: 0 30px;
    margin: 20px 0;
    font-size: 20px;
    border-radius: 3px;
    background-color: #1a243b;
    color: #c7cce6;
  }
  .leftmenus {
    width: 220px;
    margin-right: 20px;
    height: 350px;
    border-radius: 3px;
    padding: 4px;
    float: left;
    background-color: #1a243b;
    .menus {
      font-size: 14px;
    }
    .menus-item {
      height: 54px;
      line-height: 54px;
      padding-left: 38px;
      margin-bottom: 4px;
      cursor: pointer;
      img {
        margin-right: 12px;
        margin-top: -4px;
        width: 20px;
        vertical-align: middle;
      }
      span {
        color: #c7cce6;
      }
      &.active {
        color: #5697f4;
        background-color: #273142;
        border-bottom: none;
        display: block;
        span {
          color: #5697f4;
        }
      }
    }
  }
  .rightcontent {
    width: 960px;
    margin-left: 240px;
    min-height: 1000px;
    background-color: #1a243b;
  }
}
</style>



